
let lang = 'en';

const abbr = {

    en: {
        constant: 'Const',
        enumerate: 'Enum',
        variable: 'Field',
        instance: 'Inst'
    },

    cn: {
        constant: '',
        enumerate: '',
        variable: '',
        instance: ''
    }
};


function switchTo(language) {
    lang = language;
}

function getAbbr() {
    return abbr[lang];
}

export default {
    switchTo,
    getAbbr
}