<template>
    <div>
        <div>* , , </div>
        <div>* </div>
        <div>* Chrome</div>
        <div v-for="(i, index) in allInsts">
            <expression-atom :name="`${index+1}`" :variables="fields" :instances="providedInsts[index]" @alter="alterInst($event, index)"></expression-atom>
            <expression-note :instance="i" mode="code" prefix="MYSQL: "></expression-note>
            <expression-note :instance="i" mode="display" prefix=": "></expression-note>
        </div>
        <div style="cursor: pointer; font-size: 1.5em; margin-top: 40px; margin-bottom: 40px" @click="addInst">+ </div>
        <div style="height: 4px; border-bottom: 1px solid #ddd"></div>
        <div style="height: 4px; margin-top: 30px"></div>
        <div>* , </div>
        <div style="text-align: left; margin-top: 20px">
            <div style="text-indent: 40%">new Template({</div>
            <div style="text-indent: 44%">name: '',</div>
            <div style="text-indent: 44%">context: 'mysql-cn',</div>
            <div style="text-indent: 44%">templateDisplay: '($1)',</div>
            <div style="text-indent: 44%">templateCode: '$1*$1*$1',</div>
            <div style="text-indent: 44%">resultDataType: 'number',</div>
            <div style="text-indent: 44%">params: {</div>
            <div style="text-indent: 48%">$1: new Parameter({</div>
            <div style="text-indent: 52%">dataType: 'number'</div>
            <div style="text-indent: 48%">})</div>
            <div style="text-indent: 44%">}</div>
            <div style="text-indent: 40%">}).as('');</div>
        </div>
        <div style="cursor: pointer; font-size: 1.5em; margin-top: 40px; margin-bottom: 40px" @click="addOper">+ </div>
    </div>
</template>

<script>

    import ExpressionAtom from '../../mould-v/clause/ExpressionAtom'
    import ExpressionNote from '../../mould-v/explain/ExpressionNote'
    import { OP, Template, Parameter, Table, Field, Enum, Operators, i18n } from '../../mould';

    export default {

        name: 'demo-cn',

        components: {ExpressionAtom, ExpressionNote},

        data(){
            return {
                fields: [],
                allInsts: [null, null],
                providedInsts: [],
                added: false
            }
        },

        computed: {

        },

        mounted(){

            i18n.switchTo('cn');
            Operators.activateContext('mysql-cn');

            let allData = [
                new Table({
                    name: 'USER_INFO_DIM',
                    label: ''
                }).addFields([
                    new Field({
                        name: 'USER_ID',
                        label: 'ID',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'USER_NAME',
                        label: '',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'AGE',
                        label: '',
                        dataType: 'number',
                        distributeType: 'metrics',
                        unit: ''
                    }),
                    new Field({
                        name: 'GENDER',
                        label: '',
                        dataType: 'number',
                        distributeType: 'category',
                    }).addEnums([
                        new Enum({
                            code: 0,
                            label: ''
                        }),
                        new Enum({
                            code: 1,
                            label: ''
                        })
                    ])
                ]),

                new Table({
                    name: 'COMMODITY_INFO_DIM',
                    label: ''
                }).addFields([
                    new Field({
                        name: 'COMMODITY_ID',
                        label: 'ID',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'COMMODITY_NAME',
                        label: '',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'UNIT_PRICE',
                        label: '',
                        dataType: 'number',
                        distributeType: 'metrics',
                        unit: ''
                    }),
                ]),

                new Table({
                    name: 'COMMODITY_PURCHASE_LOG',
                    label: ''
                }).addFields([
                    new Field({
                        name: 'COMMODITY_ID',
                        label: 'ID()',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'USER_ID',
                        label: 'ID()',
                        dataType: 'string'
                    }),
                    new Field({
                        name: 'QUANTITY',
                        label: '',
                        dataType: 'number',
                        distributeType: 'metrics',
                        unit: ''
                    }),
                    new Field({
                        name: 'DISCOUNT',
                        label: '',
                        dataType: 'string',
                        distributeType: 'category',
                    }).addEnums([
                        new Enum({
                            code: 'none',
                            label: ''
                        }),
                        new Enum({
                            code: '0.8-all',
                            label: '8'
                        }),
                        new Enum({
                            code: '0.5-sec',
                            label: '2'
                        }),
                        new Enum({
                            code: 'free',
                            label: ''
                        })
                    ])
                ])
            ];

            this.fields = [];
            allData.forEach(t => {
                this.fields.push(...t.getFields());
            })

        },

        methods: {

            alterInst(inst, index){
                this.allInsts[index] = inst;
                this.updateProvided();
            },

            addInst(){
                this.allInsts.push(null);
                this.updateProvided();
            },

            addOper(){
                if(!this.added) {
                    new Template({
                        name: '',
                        context: 'mysql-cn',
                        templateDisplay: '($1)',
                        templateCode: '$1*$1*$1',
                        resultDataType: 'number',
                        params: {
                            $1: new Parameter({
                                dataType: 'number'
                            })
                        }
                    }).as('');
                    this.added = true;
                }
            },

            updateProvided(){
                this.$nextTick(() => {
                    let arr = [];
                    let len = this.allInsts.length;
                    this.allInsts.forEach((i, index) => {
                        arr.push(this.allInsts.slice(0, index));
                    });
                    this.providedInsts = arr;
                });
            }
        },

        watch: {


        }
    }
</script>

<style scoped>

</style>